@import url(https://fonts.googleapis.com/css2?family=Roboto:wght@300;400;500;700;900&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

:root {
    --grey-color: rgb(110, 110, 110);
}

.spacer {
    flex: 1 1;
}
.landing-container {
  font-family: Roboto;
}

.profiles-container {
  justify-content: center;
  align-items: center;
  flex-direction: column;

  display: flex;

  height: 100vh;
}

.profiles-container > .title {
  color: white;
  font-family: inherit;
  font-size: 68px;

  -webkit-user-select: none;

     -moz-user-select: none;

      -ms-user-select: none;

          user-select: none;
}

.profiles-container > .profiles {
  display: flex;
}

.profiles-container > .profiles > .profile {
  display: flex;
  flex-direction: column;

  margin: 0px 20px;
  margin-top: 40px;
  margin-bottom: 80px;
}

.profiles-container > .profiles > .profile:first-child {
  margin-left: 0px;
}

.profiles-container > .profiles > .profile:last-child {
  margin-right: 0px;
}

.profiles-container > .profiles > .profile:hover {
  cursor: pointer;
}

.profiles-container > .profiles > .profile:hover > .username {
  color: white;
}

.profiles-container > .profiles > .profile:hover > .image {
  border: 5px solid rgb(100, 100, 100);
}

.profiles-container > .profiles > .profile > .image {
  margin-bottom: 15px;

  width: 200px;
  height: 200px;

  border: 5px solid transparent;
}

.profiles-container > .profiles > .profile > .username {
  text-align: center;

  color: var(--grey-color);
  font-size: 24px;
}

/* PRIMARY BUTTON */

.primary-button {
  transition: background 200ms linear;
  border: none;
  border-radius: 3px;
  padding: 10px 40px;

  background-color: white;

  font-family: inherit;
  font-size: 24px;
  font-weight: 500;
}

.primary-button:hover {
    background-color: rgb(200, 200, 200);
}

/* SECONDARY BUTTON */

.secondary-ghost-button {
  border: 1px solid var(--grey-color);
  padding: 10px 40px;

  background: none;
  color: var(--grey-color);

  font-size: 24px;
  letter-spacing: 2px;
}

.secondary-ghost-button:hover {
  border: 1px solid white;

  color: white;
}

.secondary-button {
  transition: background 200ms linear;
  border: none;
  border-radius: 3px;
  padding: 10px 40px;

  background: rgba(120, 120, 120, 0.4);
  color: white;

  font-family: inherit;
  font-size: 24px;
  font-weight: 500;
}

.secondary-button:hover {
  background: rgba(50, 50, 50, 0.4);
}

/* DEFAULT BUTTON STYLES */

.button-container:hover {
  cursor: pointer;
}

.browse-container {
  color: white;
}

.browse-container .thumbnail-section {
  position: relative;
}

.browse-container .thumbnail-section .thumbnail {
  width: 100%;
  height: 95vh;

  background-size: cover;
  background-position: center;
}

.browse-container .thumbnail-section .description-section {
  position: absolute;

  bottom: 200px;
  left: 80px;

  max-width: 700px;
}

.browse-container .thumbnail-section .description-section .description {
  font-size: 32px;
  font-weight: 500;
}

.browse-container .thumbnail-section .description-section .actions {
  margin-top: 50px;
}

.browse-container .thumbnail-section .description-section .actions > .primary-button:first-child {
    margin-right: 15px;
}

.movie-list-container {
  position: relative;

  margin-top: -100px;
}

.movie-list-container > .title {
  font-size: 24px;
  padding-left: 80px;
}

.movie-list-container .movies {
  display: grid;

  grid-template-columns: repeat(2, 100%);

  overflow: hidden;

  scroll-behavior: smooth;
}

.movie-list-container .movies section {
  position: relative;
  display: grid;

  grid-template-columns: repeat(5, auto);

  margin: 40px 0px;
}

.movie-list-container .movies section .previous {
  z-index: 1;
  position: absolute;
  top: 0px;
  bottom: 0px;
  left: 0px;

  align-items: center;

  display: flex;
}

.movie-list-container .movies section .next {
  z-index: 1;
  position: absolute;
  top: 0px;
  right: 0px;
  bottom: 0px;
  align-items: center;

  display: flex;
}

.movie-list-container .movies section .previous {
  background: linear-gradient(to right, rgba(0, 0, 0, 0.8), transparent);
}
.movie-list-container .movies section .next {
  background: linear-gradient(to left, rgba(0, 0, 0, 0.8), transparent);
}

.movie-list-container .movies section .next svg,
.movie-list-container .movies section .previous svg {
  color: white;
  font-size: 48px;
}

.movie {
  position: relative;
  transition: transform 200ms ease-in-out, margin 200ms ease-in-out;

  margin: 0px 2px;
  height: 180px;

  background-size: cover;
  background-position: center;
}

.movie .title {
  position: absolute;
  bottom: 40px;
  left: 20px;
  opacity: 0;
}

.movie:hover {
  cursor: pointer;

  margin: 0px 40px;

  transform: scale(1.2);
}

.movie:hover .title {
  opacity: 1;
}

.navbar-container {
  z-index: 1;
  
  position: absolute;
  top: 0px;
  right: 0px;
  left: 0px;

  padding: 20px 40px;
  padding-bottom: 10px;

  align-items: center;

  display: flex;

  background: linear-gradient(to bottom, rgba(0, 0, 0, 0.7) 10%, transparent);
}

.navbar-container > .logo {
  width: 100px;
  margin-right: 40px;
}

.navbar-container > .logo:hover {
  cursor: pointer;
}

.navbar-container > .button-tertiary {
  transition: color 100ms linear;

  border: none;
  padding: 0px;
  margin: 0px 10px;

  font-family: inherit;
  font-size: 14px;
  font-weight: 400;

  background: none;
  color: rgb(230, 230, 230);
  outline: none;
}

.navbar-container > .button-tertiary:hover {
  color: rgb(180, 180, 180);
  cursor: pointer;
}

.navbar-container > .button-tertiary-active {
  font-weight: 500;

  color: white;
}

.navbar-container > .button-tertiary-active:hover {
  color: white;
  cursor: unset;
}

.navbar-container > svg {
  color: white;
  
  margin: 0px 10px;
}

.navbar-container > .profile-image {
  width: 32px;
  height: 32px;
}
